import React from 'react';

export const Input = ({input, meta, ...props}) => {
    const hasError = meta.error && meta.touched
    return (
        <div>
            <input {...input} {...props} className={hasError ? 'form-control border-error' : 'form-control'} />
            {hasError && <span className="required">{meta.error}</span>}
        </div>
    )
}
