import React, {useState} from 'react';
import Timer from "react-compound-timer";
const TimerHead = (props) => {
    let [color, setColor] = useState('');
    return (
            <div className={`timer ${color}`}>
                <Timer
                    initialTime={props.timer}
                    direction="backward"
                    formatValue={(value) => `${(value < 10 ? `0${value}` : value)}`}
                    checkpoints={[
                        {
                            time: 5000,
                            callback:() => setColor('new-timer')
                        },
                       {
                            time: 0,
                            callback:  ()=>props.logout()
                        }
                    ]}
                >
                    {() => (
                        <>  <span className="time-title">До конца сессии</span>
                            <span className="dotter">-</span>
                            <span className="time-item"><Timer.Hours formatValue={value => `${value}`} /></span>
                            <span className="dotter">:</span>
                            <span className="time-item"><Timer.Minutes /></span>
                            <span className="dotter">:</span>
                            <span className="time-item"><Timer.Seconds formatValue={value => `${value}`}/></span>
                        </>
                    )}
                </Timer>
            </div>
    );
}
export default TimerHead;


