import React from 'react';
import TimerHead from "../Timer/Timer";
import {connect} from "react-redux";
import {logout} from "../../redux/authReducer";

const TimerHeadContainer = (props) => {
    const currentTime = Math.floor(Date.now() / 1000);
    const timer = (props.expTime-currentTime)*1000;
    console.log ('newExp > '+ props.expTime);
    return (
        <TimerHead {...props} timer={timer}/>
    );
}
let mapStateToProps = (state) => {
    return {
        expTime:state.auth.expTime
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        logout: () => {
            dispatch(logout());
        }
    }
}

export default connect (mapStateToProps,mapDispatchToProps)(TimerHeadContainer)


