import React from 'react';
import {required} from "../../utils/Validators/validators";
import {reduxForm,Field} from "redux-form";
import {login} from "../../redux/authReducer";
import {connect} from "react-redux";
import Alert from "../common/Alert/Alert";
import {Input} from "../common/FormControls/Input";
import {Redirect} from "react-router-dom";

const LoginForm = ({handleSubmit, error, submitting}) => {

    return (

        <form className="justify-content-center" onSubmit={handleSubmit}>
            {error && <Alert text={error} alertType='danger'/>}
            <div className="form-group">
                <Field  name={'username'}
                        component={Input}
                        validate={[required]}
                        placeholder={'Логин'}
                        />
            </div>
            <div className="form-group">
                <Field  name={'password'}
                        component={Input}
                        type={'password'}
                        validate={[required]}
                        placeholder={'Пароль'}/>
            </div>
            <button className="btn btn-primary" type="submit" disabled={submitting}>{ submitting &&
            <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span> } Войти</button>
        </form>
    )
}
const LoginReduxForm = reduxForm({form: 'loginForm'})(LoginForm);

const Login = (props) => {
    if (props.isAuth) return <Redirect to={'/'}/>
    const onSubmit = (formData) => {
        props.login(formData)
    }


    return (
        <section className="min-vh-100 login-page">
            <div className="login-page-caption">
                <div className="container">
                    <div className="row text-white">
                        <div className="col-xl-5 col-lg-6 col-md-8 col-sm-10 mx-auto text-center form p-4">
                            <h1 className="display-6 py-2">Вход</h1>
                            <div className="px-2">
                              <LoginReduxForm onSubmit={onSubmit}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    )
};

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})
export default connect(mapStateToProps,{login})(Login)