import React from 'react';
import Header from '../Header/Header';
const Home = (props) => {
    return(
        <div className="home-page site-wrap">
            <Header />
            <div className="container">
                <h1>Привет, {localStorage.getItem('firstName')}!</h1>
                <h3>Ваша роль: {props.role}</h3>
            </div>
        </div>

    )
};
export default Home;