import {authAPI} from "../api/api";
import {stopSubmit} from "redux-form";
import {parseJwt, parseJwtFromTokenTime} from "../utils/jwt";

const SET_USER_DATA = 'SET_USER_DATA';
const LOGOUT = 'LOGOUT';

const time = parseJwtFromTokenTime();
const currentTime = Math.floor(Date.now() / 1000);
const isAuth =(time-currentTime > 0) ? true : false;

let initialState = {
    firstName: null,
    id:null,
    lastName:null,
    role:null,
    token:time,
    username:null,
    isAuth:isAuth,
    expTime:time ? time : null,
};

const authReducer = (state = initialState, action) => {

    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
                isAuth:true,
                expTime:parseJwt(action.payload.token).exp
            }
        case LOGOUT:
            return {
                ...state,
                isAuth:false,
            }

        default:
            return state;
    }
};


export const setAuthUserData = (data) => ({type: SET_USER_DATA, payload: data});
export const logout = () => ({type: LOGOUT});

export const login = (username, password) => async (dispatch) => {
    try {
        const response = await authAPI.login(username, password);
        dispatch(setAuthUserData(response.data));
        localStorage.clear();
        localStorage.setItem('token', response.data.token);
        localStorage.setItem('firstName', response.data.firstName);
        //window.location = '/'
    } catch (error) {
        if (error.response) {
            dispatch(stopSubmit('loginForm', {_error: error.response.data}))
            /*
            console.log(error.response.data);
            console.log(error.response.status);
            console.log(error.response.headers);
            */
        } else if (error.request) {
            dispatch(stopSubmit('loginForm', {_error: error.request}))
            /*
            console.log(error.request);
            */
        } else {
            dispatch(stopSubmit('loginForm', {_error: error.message}))

        }
        //dispatch(stopSubmit('loginForm', {_error: error.message}))
        /*console.log(error);*/
    }


};

export default authReducer;